or-gray-light);\n\tborder-radius: 4px;\n\tpadding: 20px;\n\tbox-shadow: 0 0 10px rgba(0, 0, 0, 0.2);\n\tz-index: 1;\n}\n.modal-text {\n\tword-wrap: break-word; /* Break long words */\n\toverflow-wrap: break-word; /* Ensure overflow wraps */\n}\n\ndialog[open] {\n\t-webkit-animation: show 0.3s ease normal;\n}\ndialog.hide {\n\t-webkit-animation: hide 0.3s ease normal;\n}\n@-webkit-keyframes show {\n\tfrom {\n\t\topacity: 0;\n\t\tscale: 0.9;\n\t}\n\tto {\n\t\topacity: 1;\n\t\tscale: 1;\n\t}\n}\n@-webkit-keyframes hide {\n\tto {\n\t\topacity: 0;\n\t\tscale: 0.9;\n\t}\n}\n\n/* Style for the close button */\n.close-modal {\n\tbackground-color: var(--color-accent);\n\tcolor: var(--color-primary);\n\tborder: none;\n\tpadding: 10px 20px;\n\tfont-size: 16px;\n\tborder-radius: 5px;\n\tcursor: pointer;\n\tmargin-top: 10px;\n}\n\n#new-task-form {\n\tborder: none;\n\tpadding: 10px;\n\tcolor: var(--color-gray);\n\tfont-weight: ;\n\tfont-size: 0.9rem;\n\tborder-radius: 5px;\n\tcursor: pointer;\n\tbackground-color: var(--color-gray-light);\n}\n#new-task-form::before {\n\tcontent: '+';\n\tfont-weight: bold;\n\tmargin-right: 5px;\n}\n\n.close-modal:hover {\n}\n\n#task-done {\n\tborder-right: 5px solid var(--color-green);\n}\n#task-not-done {\n\tborder-right: 5px solid var(--color-delete);\n}\n\n#edit-task {\n\tfont-weight: bold;\n\tmargin-bottom: 20px;\n}\n\n#delete-list {\n\tcolor: var(--color-dark);\n\n\tright: 20px;\n\tcursor: pointer;\n\tdisplay: none;\n\tfont-size: 1rem;\n\tfont-weight: bold;\n}\n\n.list-container {\n\tcursor: pointer;\n\tborder-radius: 5px;\n\tdisplay: flex;\n\tjustify-content: space-between;\n\ttransition: background-color 100ms ease;\n\tword-break: break-word;\n\toverflow-wrap: break-word;\n}\n.list-container:hover {\n\tbackground-color: var(--color-gray-light);\n}\n\n.active-list {\n\tbackground-color: var(--color-gray-light);\n\tpadding: 10px 20px;\n}\n\n@media (max-width: 900px) {\n\t.add-list-btn {\n\t\tfont-size: 0.7rem;\n\t}\n\t.container {\n\t\tpadding